import React, { Component } from 'react'

export default class About extends Component {
  render() {
    return (
      <section id="about">
        <div className="row">
          <div className="three columns">
            <img className="profile-pic" src="images/profilepic.jpg" alt />
          </div>
          <div className="nine columns main-col">
            <h2>About Me</h2>
            <p>Hi. I'm Bruno Andre, a full stack programmer based in Lima.
            I am a passionate creative driven by the synergy of code and design.
            I'm looking to add meaning to programming.
            </p>
            <div className="row">
              <div className="columns contact-details">
                <h2>Contact Details</h2>
                <p className="address">
                  <span>Bruno Morales</span><br />
                  <span>San Luis 2020<br />
                    Lima - Peru
                  </span><br />
                  <span>(+51)941-319-867</span><br />
                  <span>bruno.mor9696@gmail.com</span>
                </p>
              </div>
              <div className="columns download">
                <p>
                  <a href="#" className="button"><i className="fa fa-download" />Download Resume</a>
                </p>
              </div>
            </div>
          </div>
        </div>
      </section>
    )
  }
}
